import React from "react";
import ReactDOM from "react-dom";
import "./index.scss";
import HomePage from "./HomePage/HomePage";
import reportWebVitals from "./reportWebVitals";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import TeacherDashboard from "./teacher/TeacherDashboard";
import StudentDashboard from "./student/StudentDashboard";
import Test from "./test/test";
// import TeacherProjectBuilder from "./teacher/TeacherProjectBuilder";
import TeacherProfile from "./personalProfile/TeacherProfile";
import StudentProfile from "./personalProfile/StudentProfile";
import ProjectLibrary from "./teacher/projectLibrary/ProjectLibrary";

ReactDOM.render(
  <React.StrictMode>
    <Router>
      <Switch>
        <Route path="/student/projects" component={ProjectLibrary}></Route>
        <Route path="/teachers/projects" component={ProjectLibrary}></Route>
        <Route path="/teachers/profile" component={TeacherProfile}></Route>
        <Route path="/students/profile" component={StudentProfile}></Route>
        <Route path="/teachers" component={TeacherDashboard}></Route>
        <Route path="/students" component={StudentDashboard}></Route>
        <Route exact path="/" component={HomePage}></Route>

        <Route path="/test" component={Test}></Route>
        <Route path="*" component={HomePage} />
      </Switch>
    </Router>
  </React.StrictMode>,
  document.getElementById("root")
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
